html {
  box-sizing: border-box;
}
*, *:before, *:after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  background-color: white;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

p {
  line-height: 1.4em;
}

h1,h2,h3,h4,h5 {
  -webkit-margin-before: 0.7em;
          margin-block-start: 0.7em;
  -webkit-margin-after: 0.5em;
          margin-block-end: 0.5em; 
}

sup, sub {
  vertical-align: baseline;
  position: relative;
  top: -0.4em;
}
sub { 
  top: 0.4em; 
}

line.tick {
  stroke: black;
  stroke-width: 1px;
}

.x-axis .tick-label {
  font-size: 10px;
}

.y-axis .tick-label {
  font-size: 14px;
}

.x-axis .gridline {
  stroke: #ddd;
  stroke-width: 1px;
}

.y-axis .gridline {
  stroke: #eee;
  stroke-width: 1px;
}

.plot line {
  stroke: black;
  stroke-width: 1px;
}

.info-container {
  position: fixed;
  width: 35% ;
  border-left: 1px darkgrey solid;
  padding: 15px;
  right: 0px;
  top: 0px;
  bottom: 0px;
  background-color: #f9fafb;
  overflow-y: auto;
}

.info-legend td {
  vertical-align: top;
}

.info-legend .dot {
  height: 10px;
  width: 10px;
  border-radius: 50%;
  border: 1px grey solid;
  margin-right: 5px;
  display: inline-block;
}

.info-legend .info-legend-range {
  padding-right: 10px;
  white-space: nowrap;
}

